@import "base";

// main
//----------


// global

html,body{ height: 100%;}
html{ font-size: 320px;}
body{ min-width: 320px / 320px * 1rem; overflow-x: hidden; -webkit-font-smoothing:antialiased; -moz-user-select:none;}


/* page-spring-fest */

.page-spring-fest{ height: 100%; overflow: hidden;}


/* slide-wrap */

.slide-wrap{
 	position: relative;
 	top: 0;
 	left: 0;
 	height: 100%;

 	.list{
 		position: relative;
 		height: 100%;

 		img{
 			width:320px / 320px * 1rem;
 			height:480px / 320px * 1rem;
 		}

 		.font-img{
 			position:absolute;
 			display:inline-block;
 		}
 	}

 	.list:nth-child(1) .font-img{
 		left:80px/320px*1rem;
 		top:184px/320px*1rem;
 		width:160px/320px*1rem;
 		height:102px/320px*1rem;
 		background:url(../images/f1.png) 0 0 no-repeat;
 		background-size:160px auto;
 	}

 	.slide-wrap .list:nth-child(2){
 		
 	}

 }

